import React, { Component } from "react";
import { Link } from "react-router-dom";
//importing icons
import firebase from "../firebase";
import toastr from "toastr";
import { FaGoogle } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";

class Signin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: ""
    };

    this.handleInputEmail = this.handleInputEmail.bind(this);
    this.handleInputPassword = this.handleInputPassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputEmail(e) {
    this.setState({
      email: e.target.value
    });
  }
  handleInputPassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  signIn = (email, password) => {
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        window.location = "/";
      })
      .catch(error => {
        this.setState({
          error
        });

        toastr.error(this.state.error.message);

        // console.log(e);
      });
  };
  handleSubmit(e) {
    e.preventDefault();
    this.signIn(this.state.email, this.state.password);
  }
  render() {
    return (
      <div className="nav-item dropdown">
        <a
          className="nav-link dropdown-toggle"
          href="#"
          id="navbarDropdown"
          role="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
          onClick={this.handleLoggedIn}
        >
          Iniciar Sesión
        </a>
        <ul id="login-dp" className="dropdown-menu">
          <li>
            <div className="row">
              <div className="col-md-12">
                <form
                  className="form"
                  accept-charset="UTF-8"
                  id="login-nav"
                  onSubmit={this.handleSubmit}
                >
                  <div className="form-group">
                    <label className="sr-only" for="exampleInputEmail2">
                      Correo Electrónico
                    </label>
                    <input
                      type="email"
                      className="form-control"
                      id="exampleInputEmail2"
                      placeholder="Correo Electrónico"
                      required
                      onChange={this.handleInputEmail}
                    />
                  </div>
                  <div className="form-group">
                    <label className="sr-only" for="exampleInputPassword2">
                      Contraseña
                    </label>
                    <input
                      type="password"
                      className="form-control"
                      id="exampleInputPassword2"
                      placeholder="Contraseña"
                      required
                      onChange={this.handleInputPassword}
                    />
                  </div>
                  <div className="checkbox">
                    <label>
                      <input type="checkbox" /> Recordarme
                    </label>
                  </div>
                  <div className="help-block text-detail">
                    <Link to="/restablecer-clave">
                      Olvidaste tu contraseña?
                    </Link>
                  </div>
                  <button type="submit" className="btn btn-primary btn-block">
                    Iniciar Sesión
                  </button>
                  <hr />
                  <p className="text-center text-detail">O también puedes</p>
                  <button
                    class="btn btn-block btn-primary btn-facebook"
                    onClick={this.props.signInWithFacebook}
                  >
                    <FaFacebookF /> Continuar con Facebook
                  </button>
                  <button
                    class="btn btn-block btn-primary btn-google"
                    onClick={this.props.signInWithGoogle}
                  >
                    <FaGoogle /> Continuar con Gmail
                  </button>
                  <div className="bottom text-center mt-2 text-detail">
                    Aún no tienes cuenta?
                    <br />
                    <Link to="/registro">
                      <b>Regístrate Ahora</b>
                    </Link>
                  </div>
                </form>
              </div>
            </div>
          </li>
        </ul>
      </div>
    );
  }
}

export default Signin;
