import React, { Component } from "react";
import { Link } from "react-router-dom";
import SignedOutLinks from "./SignedOutLinks";
import SignedInLinks from "./SignedInLinks";
import Signin from "../auth/Signin";

// importing icon
import { FiNavigation2 } from "react-icons/fi";

class Navbar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div
        id="main-nav"
        className="navbar navbar-expand-lg navbar-light bg-light"
      >
        <div className="container">
          <Link to="/" className="navbar-brand" href="#">
            <FiNavigation2 className="mb-1 logo-color-a" />
            rquibusco
          </Link>
          {/* <!-- Hamburger icon --> */}
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          {/* <!-- Content inside hamburger icon --> */}
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {/* importing signed out links */}
            <SignedOutLinks />
            {/* <!-- Sign in dropdown --> */}
            {!this.props.user ? (
              <Signin
                handleToggle={this.handleToggle}
                signInfunction2={this.props.signInfunction}
                signInWithGoogle={this.props.signInWithGoogle}
                signInWithFacebook={this.props.signInWithFacebook}
              />
            ) : (
              <SignedInLinks user={this.props.user} />
            )}

            {/* <!-- Importing Signed In Links --> */}
            {/* className={this.state.isLoggedIn ? ""} */}
          </div>
        </div>
      </div>
    );
  }
}

export default Navbar;
