import React from 'react'

// importing config files
import ConfigNavbar from '../components/Profile Config/ConfigNavbar'
import General from '../components/Profile Config/General';
import Contrasena from '../components/Profile Config/Contrasena';
import Privacidad from '../components/Profile Config/Privacidad';


// panel de configuracion
const Configuracion = () => {
    return (
        <div>
            <div className="container p-0">
                <h1 className="h3 mb-3 mt-5 text-center">Configuración de Perfil</h1>
                <hr className="mt-5 mb-5" />
                <div className="row mt-5">
                    {/*------- nav -------*/}
                    <div class="col-md-5 col-xl-4">
                        <ConfigNavbar />
                    </div>

                    {/*------- main form -------*/}
                    <div className="col-md-7 col-xl-8">
                        <div className="tab-content">
                            <General />
                            <Contrasena />
                            <Privacidad />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Configuracion
