import React from 'react'

const RestorePassword = () => {
    return (
        <div className="m-auto p-3 text-center d-flex align-items-center" id="restore-password">
            <div className="align-middle">
                <h1>Restablece tu Contraseña</h1>
                <hr />
                <p>Te enviaremos un correo electrónico con los siguientes pasos.</p>
                <form className="form" role="form" method="post" action="login" accept-charset="UTF-8"
                    id="password-form">
                    <div className="form-group">
                        <label className="sr-only" for="exampleInputEmail2">Correo Electrónico</label>
                        <input type="email" className="form-control" id="exampleInputEmail2"
                            placeholder="Correo Electrónico" required />
                    </div>
                    <button type="submit" className="btn btn-primary btn-block">Enviar Correo</button>
                </form>
            </div>
        </div>







    )
}

export default RestorePassword
