import React from 'react'
import { NavLink } from 'react-router-dom'

const SignedOutLinks = () => {
    return (
        <ul className="navbar-nav mr-auto">
            <li className="nav-item">
                <NavLink to='/categorias' className="nav-link" >
                    Categorías</NavLink></li>
            <li className="nav-item"> <NavLink to='/como-funciona' className="nav-link" >Cómo Funciona</NavLink></li>
            <li className="nav-item"> <NavLink to='/encuentra' className="nav-link">Encuentra un Diseñador</NavLink></li>
        </ul>
    )
}

export default SignedOutLinks