import React from 'react'

const EmailSent = () => {
    return (
        <div className="m-auto p-3 text-center d-flex align-items-center" id="email-sent">
            <div className="align-middle">
                <h1>Listo!</h1>
                <hr />
                <p>En breve te estará llegando nuestro correo. No olvides revisar también la bandeja de spam o correo no deseado.</p>
            </div>

        </div>
    )
}

export default EmailSent
