import React, { Component } from 'react'

//importando el hero
import HeroCoverPage from '../components/HeroCoverPage';

//importando el Como Funciona
import QuoteMachine from '../components/QuoteMachine'

//importando los quotes
import ComoFunciona from '../components/ComoFunciona'

//importando el carousel slider
import CarouselSlider from '../components/Carousel/CarouselSlider'



class Home extends Component {
    render() {
        return (

            <>
                <HeroCoverPage />
                <CarouselSlider />
                <ComoFunciona />
                <QuoteMachine />

            </>
        )
    }

}

export default Home
