import React from 'react'
import selectDesigner from '../assets/img/Banners/selectdesigner2.png'
import publishWork from '../assets/img/Banners/publishwork2.png'


function ComoFunciona() {
    return (
        <section id="index-comofunciona">
            <h2 className="text-center mt-5 mb-5">¿Cómo Funciona?</h2>
            <hr className="mb-5" />
            <div className="container">
                <div className="row w-100  text-center">
                    <div className="col-lg-6 col-md-12 como-funciona-main">
                        <div className="container como-funciona">
                            <img src={selectDesigner} alt="" />
                        </div>
                        <p className="font-weight-bold text-landing">TRABAJA CON UN DISEÑADOR</p>
                        <p className="text-landing">¡Explora los diferentes portafolios <br /> y solicita colaborar con tu preferido!</p>
                        <a href="#" className="btn btn-outline-primary">Encuentra un diseñador</a>
                    </div>
                    <div className="col-lg-6 col-md-12 columna-2">
                        <div className="container como-funciona">
                            <img src={publishWork} alt="" />
                        </div>
                        <p className="font-weight-bold text-landing">PUBLICA TU PROYECTO</p>
                        <p className="text-landing">¡Déjale saber a la comunidad qué necesitas <br /> y los expertos te contactarán a ti!</p>
                        <a href="#" className="btn btn-outline-primary">Postea un proyecto</a>
                    </div>
                </div>
            </div>

        </section>
    )
}

export default ComoFunciona
