import React from 'react'

// importing icons
import { FaFacebookF } from 'react-icons/fa'
import { FaInstagram } from 'react-icons/fa'

const fullYear = () => {
    return new Date().getFullYear()
}

const Footer = () => {
    return (
        <div className="footer-inicial">
            <footer>
                <div className="social">
                    <a href="#"><FaInstagram /></a>
                    {/* <a href="#"><i className="icon ion-social-snapchat"></i></a>
                    <a href="#"><i className="icon ion-social-twitter"></i></a> */}
                    <a href="#"><FaFacebookF /></a>
                </div>
                <ul className="list-inline">
                    <li className="list-inline-item"><a href="#">Inicio</a></li>
                    <li className="list-inline-item"><a href="#">Servicios</a></li>
                    <li className="list-inline-item"><a href="#">Sobre Nosotros</a></li>
                    <li className="list-inline-item"><a href="#">Términos y condiciones</a></li>
                    <li className="list-inline-item"><a href="#">Política de privacidad</a></li>
                </ul>
                <p className="copyright">Arquibusco &copy; {fullYear()}</p>
            </footer>
        </div >
    )
}


export default Footer

