import React from 'react'

//importing images
import bannerPicture from '../assets/img/Banners/banner-photo3.png'

const HeroCoverPage = () => {
    return (
        <div className="default-hero">
            <div className="container">
                <div id="hero-area" className="row w-100 h-100 align-items-center">
                    <div id="hero-info" className="col-md-5">
                        <h1 class="mb-3">Comunidad creativa de talento local</h1>
                        <p className="text-landing">
                            Conecta con <span className="text-primary">
                                <strong className="u-text-animation u-text-animation--typing">el diseñador </strong>
                                <span className="typed-cursor">|</span></span><br /> ideal para cada uno de tus proyectos!
                    </p>
                        <a href="#" className="btn btn-primary btn-hero">Empieza Aquí</a>
                    </div>
                    <div id="hero-img" className="col-md-7 d-none d-md-block"> <img src={bannerPicture} alt="" /> </div>
                </div>
            </div>
        </div>

    )
}

export default HeroCoverPage
