import React, { Component } from "react";
//importing icons
import { FaGoogle } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";
import firebase from "../firebase/";
import { Link } from "react-router-dom";
import toastr from "toastr";

class SignIn extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      error: {}
    };

    this.handleInputEmail = this.handleInputEmail.bind(this);
    this.handleInputPassword = this.handleInputPassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputEmail(e) {
    this.setState({
      email: e.target.value
    });
  }
  handleInputPassword(e) {
    this.setState({
      password: e.target.value
    });
  }
  signIn = (email, password) => {
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .then(() => {
        window.location = "/";
      })
      .catch(error => {
        this.setState({
          error
        });

        toastr.error(this.state.error.message);

        // console.log(e);
      });
  };
  handleSubmit(e) {
    e.preventDefault();
    this.signIn(this.state.email, this.state.password);
  }

  render() {
    return (
      // <div className="signin-photo d-flex align-items-center">
      //   <div className="form-container align-middle">
      //     <div className="row">
      //       <div className="col-md-6">
      //         <div className="image-holder" />
      //       </div>
      //       <div className="col-md-6">
      //         <form
      //           className="form"
      //           accept-charset="UTF-8"
      //           id="login-nav"
      //           onSubmit={this.handleSubmit}
      //         >
      //           <h1>Inicia Sesión</h1>
      //           <div className="form-group">
      //             <label className="sr-only" for="exampleInputEmail2">
      //               Correo Electrónico
      //             </label>
      //             <input
      //               type="email"
      //               className="form-control"
      //               id="exampleInputEmail2"
      //               placeholder="Correo Electrónico"
      //               required
      //               onChange={this.handleInputEmail}
      //             />
      //           </div>
      //           <div className="form-group">
      //             <label className="sr-only" for="exampleInputPassword2">
      //               Contraseña
      //             </label>
      //             <input
      //               type="password"
      //               className="form-control"
      //               id="exampleInputPassword2"
      //               placeholder="Contraseña"
      //               required
      //               onChange={this.handleInputPassword}
      //             />
      //           </div>
      //           <button type="submit" className="btn btn-primary btn-block">
      //             Iniciar Sesión
      //           </button>
      //         </form>
      //         <div className="otherMethods">
      //           <hr />
      //           <p className="text-center">o también puedes</p>
      //           <button
      //             class="btn btn-block btn-primary btn-facebook"
      //             onClick={this.props.signInWithFacebook}
      //           >
      //             <FaFacebookF /> Continuar con Facebook
      //           </button>
      //           <button
      //             class="btn btn-block btn-primary btn-google"
      //             onClick={this.props.signInWithGoogle}
      //           >
      //             <FaGoogle /> Continuar con Gmail
      //           </button>
      //           <div className="bottom text-center mt-2">
      //             Aún no tienes cuenta? <br />
      //             <Link to="/registro">Regístrate Ahora!</Link>
      //           </div>
      //         </div>
      //       </div>
      //     </div>
      //   </div>
      // </div>
      <div className="signin-photo d-flex align-items-center">
        <div className="form-container align-middle">
          <div className="image-holder" />
          <form
            className="form"
            role="form"
            onSubmit={this.handleSubmit}
            accept-charset="UTF-8"
            id="login-nav"
          >
            <h1>Inicia Sesión</h1>
            <div className="form-group">
              <label className="sr-only" for="exampleInputEmail2">
                Correo Electrónico
              </label>
              <input
                type="email"
                className="form-control"
                id="exampleInputEmail2"
                placeholder="Correo Electrónico"
                required
                onChange={this.handleInputEmail}
              />
            </div>
            <div className="form-group">
              <label className="sr-only" for="exampleInputPassword2">
                Contraseña
              </label>
              <input
                type="password"
                className="form-control"
                id="exampleInputPassword2"
                placeholder="Contraseña"
                required
                onChange={this.handleInputPassword}
              />
            </div>
            <button type="submit" className="btn btn-primary btn-block">
              Iniciar Sesión
            </button>
            <hr />
            <p className="text-center">o también puedes</p>
            <button
              type="button"
              class="btn btn-block btn-primary btn-facebook"
              onClick={this.props.signInWithFacebook}
            >
              <FaFacebookF /> Continuar con Facebook
            </button>
            <button
              class="btn btn-block btn-primary btn-google"
              type="button"
              onClick={this.props.signInWithGoogle}
            >
              <FaGoogle /> Continuar con Gmail
            </button>
            <div className="bottom text-center mt-2">
              Aún no tienes cuenta? <br />
              <a href="#">Regístrate Ahora!</a>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default SignIn;
