import React, { Component } from "react";
import { Redirect } from "react-router-dom";
//importing main css file + bootstrap
import "./main.scss";

//importing views
import Inicio from "./views/Inicio";
import Perfil from "./views/Perfil";
import Categorias from "./views/Categorias";
import ComoFunciona from "./views/ComoFunciona";
import Encuentra from "./views/Encuentra";
import Error from "./views/Error";

//importing from auth
import Signup from "./auth/Signup";
import SigninPage from "./auth/SigninPage";
import RestorePassword from "./auth/RestorePassword";
import EmailSent from "./auth/EmailSent";

//importing route
import { Route, Switch } from "react-router-dom";

//importing layout
import Navbar from "./layout/Navbar";
import Footer from "./layout/Footer";
import Configuracion from "./views/Configuracion";

//import firebase

import firebase from "./firebase";

const PrivateRoute = ({ component: Component, authed, rest }) => (
  <Route
    {...rest}
    render={props =>
      authed === true ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/iniciosesion",
            state: { from: props.location }
          }}
        />
      )
    }
  />
);

const PublicRoute = ({ component: Component, authed, rest }) => (
  <Route
    {...rest}
    render={props =>
      authed === false ? <Component {...props} /> : <Redirect to="/" />
    }
  />
);

class App extends Component {
  state = {
    user: null,
    authed: false
  };

  signIn(email, password) {
    firebase.auth().signInWithEmailAndPassword(email, password);
  }

  signInWithGoogle() {
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then(() => {
        // window.location = "/";
      })
      .catch(e => {
        console.log(e);
      });
  }
  signInWithFacebook() {
    const provider = new firebase.auth.FacebookAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then(() => {
        // window.location = "/";
      })
      .catch(e => {
        console.log(e);
      });
  }

  componentDidMount() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.setState({
          user,
          authed: true
        });
      } else {
        console.log("no existe usuario");
        this.setState({ authed: false });
      }
    });
  }

  render() {
    return (
      <div>
        <Navbar
          user={this.state.user}
          signInWithGoogle={this.signInWithGoogle}
          signInWithFacebook={this.signInWithFacebook}
          signInfunction={this.signIn}
        />
        <Switch>
          <Route exact path="/" component={Inicio} />
          <PublicRoute
            authed={this.state.authed}
            path="/registro"
            component={Signup}
          />
          {/* <Route path="/registro" component={() => <Signup />} /> */}
          {/* <Route
            path="/iniciosesion"
            component={() => (
              <SigninPage
                signInFunction={this.signIn}
                signInWithGoogle={this.signInWithGoogle}
                signInWithFacebook={this.signInWithFacebook}
              />
            )}
          /> */}
          <PublicRoute
            authed={this.state.authed}
            path="/iniciosesion"
            component={() => (
              <SigninPage
                signInFunction={this.signIn}
                signInWithGoogle={this.signInWithGoogle}
                signInWithFacebook={this.signInWithFacebook}
              />
            )}
          />
          <Route path="/restablecer-clave" component={RestorePassword} />
          <Route path="/correo-enviado" component={EmailSent} />
          <Route exact path="/categorias" component={Categorias} />
          <Route exact path="/encuentra" component={Encuentra} />
          <Route exact path="/como-funciona" component={ComoFunciona} />
          <Route exact path="/perfil/:slug" component={Perfil} />
          <Route path="/usuario/:usuario" component={Configuracion} />
          <Route component={Error} />
        </Switch>
        <Footer />
      </div>
    );
  }
}

export default App;
