import React from "react";
import { NavLink } from "react-router-dom";
import firebase from "../firebase";

//importing icons
import { FaUser } from "react-icons/fa";

const signOut = async props => {
  try {
    await firebase.auth().signOut();
    console.log("cerro sesion");
    window.location = "/";
  } catch (e) {}
};

const SignedOutLinks = props => {
  return (
    <ul className="navbar-nav mt-2 mt-md-0 center-sign">
      <li className="nav-item" />
      <li className="nav-item">
        <NavLink to="/">
          <button className="btn btn-light mr-1">
            {/* <i className="fas fa-user"></i> Perfíl */}
            <FaUser className="mb-1" />{" "}
            {props.user.displayName
              ? props.user.displayName.split(" ")[0]
              : props.user.email}
          </button>
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink to="/">
          <button className="btn btn-outline-success mr-1">
            Mensajes <span className="badge badge-light">3</span>
          </button>
        </NavLink>
      </li>
      <li className="nav-item">
        <button className="btn btn-outline" onClick={signOut}>
          Finalizar sesion
        </button>
      </li>
    </ul>
  );
};

export default SignedOutLinks;
