import React from 'react'


function Hero({ hero, children }) {
    // children: queremos poder accesar los hijos de Hero (botones, fotos, etc...) 53:35
    //hero: ownProps para poder utilizarlo cada vez que quiera cambiar el className

    return (
        <header className={hero}>
            {/* cover page */}
            {children}
        </header>
    )
}

export default Hero


