import React, { Component } from "react";
//importing icons
import { FaGoogle } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";
import firebase from "../firebase/";
import { Link } from "react-router-dom";
import toastr from "toastr";

class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      emailConfirmation: "",
      password: "",
      nombreCompleto: "",
      error: {}
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleInputChange(e) {
    this.setState({
      [e.target.id]: e.target.value
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    // this.props.signUpFunction(this.state.email, this.state.password);
    if (this.state.emailConfirmation === this.state.email) {
      this.signUp(this.state.email, this.state.password);
    } else {
      toastr.error("los correos deben coincidir!");
    }
  }
  signUp = (email, password) => {
    firebase
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .then(() => {
        console.log("creado satisfactoriamente");
        this.sendEmailConfirmation();
        this.updateProfile();
        window.location = "/";
      })
      .catch(error => {
        this.setState({
          error
        });
        toastr.error(this.state.error.message);
        console.log(error);
      });
  };

  updateProfile() {
    let user = firebase.auth().currentUser;

    user.updateProfile({
      displayName: this.state.nombreCompleto
    });
  }

  sendEmailConfirmation() {
    let user = firebase.auth().currentUser;

    user
      .sendEmailVerification()
      .then(() => {
        console.log("enviando correo...");
      })
      .catch(error => {
        this.setState({
          error
        });
        console.log(error);
      });
  }

  render() {
    return (
      <div className="register-photo d-flex align-items-center">
        <div className="form-container align-middle">
          <div className="image-holder" />
          <form
            className="form"
            onSubmit={this.handleSubmit}
            accept-charset="UTF-8"
            id="login-nav"
          >
            <h1>Regístrate</h1>
            <div className="form-group">
              <label className="sr-only" for="nombreCompleto">
                Usuario
              </label>
              <input
                type="text"
                className="form-control"
                id="nombreCompleto"
                onChange={this.handleInputChange}
                placeholder="Nombre completo"
                required
              />
            </div>
            <div className="form-group">
              <label className="sr-only" for="email">
                Correo Electrónico
              </label>
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Correo Electrónico"
                required
                onChange={this.handleInputChange}
              />
            </div>
            <div className="form-group">
              <label className="sr-only" for="emailConfirmation">
                Correo Electrónico
              </label>
              <input
                type="email"
                className="form-control"
                id="emailConfirmation"
                placeholder="Confirmar Correo Electrónico"
                required
                onChange={this.handleInputChange}
              />
            </div>
            <div className="form-group">
              <label className="sr-only" for="password">
                Contraseña
              </label>
              <input
                type="password"
                className="form-control"
                id="password"
                placeholder="Contraseña"
                required
                onChange={this.handleInputChange}
              />
            </div>
            <button type="submit" className="btn btn-primary btn-block">
              Continuar
            </button>
            <hr />
            <p className="text-center text-detail mb-0">
              o también puedes registrarte con
            </p>
            <div className="row">
              <div className="col-sm-6">
                <button class="btn btn-block btn-primary btn-facebook">
                  <FaFacebookF /> Facebook
                </button>
              </div>
              <div className="col-sm-6">
                <button class="btn btn-block btn-primary btn-google">
                  <FaGoogle /> Gmail
                </button>
              </div>
            </div>
            <div className="text-center text-detail mt-3">
              Al continuar, acepto los <a href="#">términos de uso</a> y la{" "}
              <a href="#">política de privacidad</a>{" "}
            </div>
            <div className="bottom text-center text-detail">
              Ya tienes una cuenta?
              <Link to="/iniciosesion">Inicia sesión aquí</Link>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default Signup;
