import React from 'react'

//importando el hero
import GenericHero from '../components/GenericHero'

//importando el banner
import Banner from '../components/Banner'

//importing others
import { Link } from 'react-router-dom'

function Perfil() {
    return (

        <GenericHero hero="perfil-hero">
            <Banner title="Perfiles" subtitle="Esto es un banner sample que se puede modificar. se supone que en este route van los perfiles individuales.">
                <Link to="/" className="btn btn-primary">This is a link</Link>
            </Banner>

        </GenericHero>
    )
}

export default Perfil
