import React from 'react'

function CarouselSliderCard({ cards }) {

    const cardsArray = cards.map(card => {
        return (
            <div className="card--content" key={card.id}>
                <img className="img-cat" src={`${card.img}`} alt="" />
                <p className="text-category">{card.title}</p>
            </div>
        )
    }

    )

    return (
        <div>
            {cardsArray}
        </div>
    )
}

export default CarouselSliderCard


